<template>
  <div class="about">
    <div class="content">
        <img :src="imgUrl" alt="">
        <h6>{{title}}</h6>
    </div>
    <p>{{summary}}</p>
  </div>
</template>
<script>
import axios from "axios-jsonp-pro";
    export default {
        name:"about",
        data(){
            return{
                imgUrl:"",
                title:"",
                summary:"",
            };
        }, 
        mounted(){ 
            var id=this.$route.params.id; 
            var url="https://douban.uieee.com/v2/movie/subject/";
            axios.jsonp(url+id).then(res=>{
                this.imgUrl=res.images.small;
                this.title=res.title;
                this.summary=res.summary;
            });
        }
    };
</script>
<style lang="scss" scoped>
    .content{
        text-align: center;
    }
    .about{
        max-width: 800px;
        padding:20px;
        margin-left: auto;
        margin-right: auto;
    }
</style>
